import React from "react";

export default function MainTabs({ clickHandler }) {
  return (
    <div className="MainTabs">
      <button
        onClick={() =>
          clickHandler(<i className="fas fa-smile-wink">Produkty dla niej</i>)
        }
      >
        DLA NIEJ
      </button>
      <button
        onClick={() =>
          clickHandler(<i className="fas fa-smile-wink">Produkty dla niego</i>)
        }
      >
        DLA NIEGO
      </button>
    </div>
  );
}
